.task-item {
  border-style: solid;
  border-width: 2px;
  border-radius: 4px;
  width: 100%;
  text-align: left;
  /*grid-column: 2;*/
}

#task-container {
  margin-top: 400px;
  display: grid;
  /*border-style: solid;*/
  /*border-color: lightgray;*/
  width: 100%;
  grid-column: 2/ span 3;
  gap: 20px;
}

.buttons-and-tasks {
  display: grid;
  grid-template-columns: 50px auto auto auto 50px;
  grid-template-rows: auto 25px auto;
  width: 100%;
  gap: 15px;
}

body {
  font-family: Futura;
  background: whitesmoke;
  text-align: center;
}

h2 {
  /*background-color: red;*/
  font-size: 700%;
  margin-bottom: 0px;
  text-align: center;
  align-items: center;
  justify-content: center;
}

input.check {
  width: 35px;
  height: 35px;
  margin-right: 10px;
}

label {
  font-size: 400%;
}

button {
  font-family: Futura;
  font-size: 173%;
  color: black;
  background: #e3e7ff;
  border-color: cornflowerblue;
  border-radius: 4px;
  border-style: solid;
  font-size: 25pt;
}

button:hover {
  background-color: #bfc7f5;
  transition: background-color .2s;
}

.checkmark {
  float: left;
  size: 26px;
}

.menu-buttons {
  width: 200px;
  height: 100px;
  font-size: 38px;

}

.menu-buttons-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 20pt;
  grid-column: 2 /span 3;
  width: 100%;
}

#new-item-button {
  grid-column: 2;
}

#view-button {
  grid-column: 3;
}

#trash-button {
  grid-column: 4;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  display: inline-block;
  width: 200px;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  font-size:30px;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {background-color: #ddd;}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {display: block;}

/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdown:hover {
  background-color: #bfc7f5;
  transition: background-color .2s;
}

.dropdown-item {
  width: 100%;
  padding: 10px;
  background-color: #f1f1f1;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  font-size:30px;
  border: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-item:hover {
  background-color: #ddd;
  transition: background-color .2s;
}

.small-triangle {
  font-size: 66%;
}

#app-header {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

form input{
  font-size: 200%;
  border-style: none;
}

#done-button {
  margin-top: 10px;
}

#new-item {
  width: 50%;
}

#new-item-label {
  font-size: 45px;
}

#task-text-entry {
  width: 650px;
  box-sizing: border-box;
  background-color: transparent;
  box-shadow: none;
  font-size: 40pt;
  border: none;
  user-select: all;
  margin-top: 15px;
}

TextField {
  outline: none;
}

.app {
  width: 640px;
}

.backdrop {
  position: fixed;
  top:0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
}

.modal {
  background-color: #fff;
  border-radius: 5pt  ;
  border-color: black;
  border-style: solid;
  margin: 0 auto;
  top: 500pt;
  height: 160pt;
  width: 350pt;
  padding: 10pt;
  position: relative;
  font-size: 30pt;
}

#priority {
  font-size: 30px;
  font-family: Futura;
  background: lightgrey;
  margin-bottom: 25px;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
  width:100px;
  border-radius: 10px;
}

#task-sorting {
  font-size: 20px;
  font-family: Futura;
  background: #e3e7ff;
  border-color: cornflowerblue;
  border-radius: 5px;
  margin-bottom: 25px;
}


#sorting-area {
  display: grid;
  grid-column: 3/ span 2;
  text-align: center;
  float: right;
  margin-right: 85px;
  margin-top: 15px;
}

#sort {
  display: flex;
  flex-direction: row;
  font-size: 25px;
  font-family: Futura;
}

#sort-label {
  margin-right: 10px;
}

#task-color-a-false {
  background: #ff8e8e;
}

#task-color-b-false {
  background: #fff3a6;
}

#task-color-c-false {
  background: #c5e8b0;
}

#task-color-a-true {
  background: grey;
}
#task-color-b-true {
  background: grey;
}
#task-color-c-true {
  background: grey;
}

#fixed-buttons {
  position: sticky;
  top: 0px;
  background: whitesmoke;
  position: fixed;
  width: 100%;
  z-index: 100;
}

.start {
  margin-bottom: 5px;
}

